import React from "react";
import "bulma/css/bulma.css";
import "./App.css";
import { EditorScene } from "./components/Editor";
import { Provider } from "react-redux";
import { appStore } from "./immerReducers/appStore";
// import { buildBead } from "./editorObjects/Bead";
// import { Actions } from "./immerReducers/store";
import {
  HashRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
} from "react-router-dom";
import { renderThreejs, unmountThreejs } from "./threejs/renderThreejs";
// import { sendEvent } from "./controllers/storeController";
// import { AppEventType } from "./controllers/AppEventType";
import { clearBoard, prepareBoard } from "./prepareBoard";

export interface AppData {
  apiHost: string;
  user: { id: string; username: string };
  token: string;
}
const App: React.FC<{
  data: AppData;
}> = (props) => {
  console.log("render App.tsx");
  return <Routes data={props.data} />;
};

const Routes: React.FC<{
  data: AppData;
}> = (props) => {
  return (
    <Router hashType={"slash"}>
      <Switch>
        <Route path="/boards">
          <PageBoards data={props.data} />
        </Route>
        <Route path="/board/:id">
          <PageBoard data={props.data} />
        </Route>
        <Route path="/">
          <PageHome data={props.data} />
        </Route>
      </Switch>
    </Router>
  );
};

const PageHome: React.FC<{
  data: AppData;
}> = () => {
  return (
    <div>
      <h1>Welcome</h1>
      <ul>
        <li>
          <Link to="/boards">Boards</Link>
        </li>
      </ul>
    </div>
  );
};

const PageBoards: React.FC<{
  data: AppData;
}> = () => {
  return (
    <div>
      <h2>Boards</h2>

      <ul>
        <li>
          <Link to="/board/1234">Board1</Link>
        </li>
        <li>
          <Link to="/board/demo">DemoBoards</Link>
        </li>
      </ul>
      <Link to="/">Back Home</Link>
    </div>
  );
};

const PageBoard: React.FC<{
  data: AppData;
}> = (props) => {
  // React.useEffect(() => {
  // appStore.dispatch({
  //   type: "startAppEvent",
  //   payload: { state: appStore.getState() },
  // });

  // appStore.dispatch(Actions.Persist.action_readSavedCanvas());
  // }, []);

  let { id } = useParams<{ id: string }>();

  React.useEffect(() => {
    renderThreejs(document.getElementById("threejs"));
    prepareBoard(id, props.data.apiHost);

    return () => {
      clearBoard(id);
      unmountThreejs(document.getElementById("threejs"));
    };
  }, []);

  return (
    <div className="App">
      <Provider store={appStore}>
        <EditorScene />
      </Provider>
    </div>
  );
};

export default App;

