console.log("start app with version: ", packageJson.version);
const root = document.getElementById("root");
if (root !== null) {
  try {
    let data: AppData = {
      apiHost: "http://localhost:9000/",
      token: "123",
      user: {
        id: "123",
        username: "username",
      },
    };
    const hash = root.getAttribute("data-hash");
    if (hash) {
      data = JSON.parse(hash);
    }
    ReactDOM.render(<App data={data} />, root);
  } catch (err) {}
}

